/* MapLayers.tsx */

.container {
    @apply p-2 mt-2 px-4 w-64 h-auto rounded bg-white dark:bg-neutral-800 block ml-4 max-h-80 overflow-auto;
}

.optionContainer {
    @apply text-left text-sm font-medium pb-2 dark:text-white;
}

.marker {
    @apply cursor-pointer h-20 w-full rounded-md object-cover outline-2 outline-offset-4 outline-blue-400;
}

.layer-text {
    @apply text-xs dark:text-white;
}

/* OptionButton.tsx */

.circle-button-container {
    @apply flex absolute left-10 z-[1000];
}

.circle-button {
    @apply p-3 rounded-full drop-shadow-2xl bg-white flex items-center justify-center w-12 h-12;
}

.title {
    @apply font-medium;
}
