/* SelectButton.tsx */
.form-control option {
    background: transparent !important;
}
.form-control {
    background: transparent !important;
    cursor: pointer;
}
.form-control .option {
    cursor: pointer;
}

.dropdown {
    width: fit-content;
    border-radius: 10px;
    box-shadow: 0 10px 25px rgba(0, 0, 0, 0.1);
    background-color: white;
}

.dropdown-header {
    padding: 15px;
    cursor: pointer;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.dropdown-body {
    padding: 5px;
    border-top: 1px solid #e5e8ec;
    display: none;
    position: absolute;
    background-color: white;
    z-index: 1000;
}

.dropdown-body.open {
    display: block;
}

.dropdown-item {
    padding: 10px;
}

.dropdown-item:hover {
    cursor: pointer;
}

.dropdown-item-dot {
    opacity: 0;
    color: #000000;
    transition: all 0.2s ease-in-out;
}

.dropdown-item-dot.selected {
    opacity: 1;
}

.icon {
    font-size: 13px;
    color: #91a5be;
    transform: rotate(0deg);
    transition: all 0.2s ease-in-out;
}

.icon.open {
    transform: rotate(90deg);
}
