.ol-map {
    min-width: 600px;
    min-height: 500px;
    height: 100vh;
    width: '100%';
}
.ol-control {
    position: absolute;
    background-color: rgba(255, 255, 255, 0.4);
    border-radius: 4px;
    padding: 2px;
}
.ol-full-screen {
    top: 0.5em;
    right: 0.5em;
    background: white;
    padding: 0.5rem 1rem;
}

.ol-full-screen:hover {
    background-color: lightgray;
}

.ol-zoom-in {
    padding: 0.25rem 0.8rem 0.25rem 0.8rem;
    font-weight: bold;
}

.ol-zoom-in:hover {
    background-color: lightgray;
}

.ol-zoom-out {
    padding: 0.25rem 0.8rem 0.25rem 0.8rem;
    font-weight: bold;
}

.ol-zoom-out:hover {
    background-color: lightgray;
}

.ol-zoom {
    bottom: 50px;
    left: 25px;
    font-weight: bold;
    background: white;
    transform: rotate(90deg);
}
