.ol-scale-line-inner {
    border: 2px solid rgb(255, 255, 255);
    border-top: none;
    color: #eee;
    font-size: 0.7rem;
    text-align: center;
    margin: 1px;
    will-change: contents, width;
    transition: all 0.25s;
    bottom: 0;
    backdrop-filter: blur(5px);
    bottom: 3%;
    left: 6%;
    position: absolute;
    border-radius: 3px;
}